////
/// @group global
////

/// Font size attribute applied to `<html>` and `<body>`. We use 100% by default so the value is inherited from the user's browser settings.
/// @type Number
$global-font-size: 100% !default;

/// Global width of your site. Used by the grid to determine row width.
/// @type Number
$global-width: rem-calc(1200) !default;

/// Default line height for all type. `$global-lineheight` is 24px while `$global-font-size` is 16px
/// @type Number
$global-lineheight: 1.5 !default;

/// Primary color for interactive components like links and buttons.
/// @type Color
$primary-color: #2199e8 !default;

/// Secondary color, used with components that support the `.secondary` class.
/// @type Color
$secondary-color: #777 !default;

/// Color to indicate a positive status or action, used with the `.success` class.
/// @type Color
$success-color: #3adb76 !default;

/// Color to indicate a caution status or action, used with the `.warning` class.
/// @type Color
$warning-color: #ffae00 !default;

/// Color to indicate a negative status or action, used with the `.alert` class.
/// @type Color
$alert-color: #ec5840 !default;

/// Color used for light gray UI items.
/// @type Color
// $light-gray: #e6e6e6 !default
$light-gray: #0F3D52 !default;

/// Color used for medium gray UI items.
/// @type Color
$medium-gray: #cacaca !default;

/// Color used for dark gray UI items.
/// @type Color
$dark-gray: #8a8a8a !default;

/// Color used for black ui items.
/// @type Color
$black: #0a0a0a !default;

/// Color used for white ui items.
/// @type Color
$white: #fefefe !default;

/// Background color of the body.
/// @type Color
$body-background: $white !default;

/// Text color of the body.
/// @type Color
$body-font-color: $black !default;

/// Font stack of the body.
/// @type List
$body-font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif !default;

/// Set to `true` to enable antialiased type, using the `-webkit-font-smoothing` and `-moz-osx-font-smoothing` CSS properties.
/// @type Boolean
$body-antialiased: true !default;

/// Global value used for margin on components.
/// @type Number
$global-margin: 1rem !default;

/// Global value used for padding on components.
/// @type Number
$global-padding: 1rem !default;

/// Global font weight used for normal type.
/// @type Keyword | Number
$global-weight-normal: normal !default;

/// Global font weight used for bold type.
/// @type Keyword | Number
$global-weight-bold: bold !default;

/// Global value used for all elements that have a border radius.
/// @type Number
$global-radius: 0 !default;

/// Sets the text direction of the CSS. Can be either `ltr` or `rtl`.
$global-text-direction: ltr !default;

// Internal variables used for text direction
$global-left: if($global-text-direction == rtl, right, left);
$global-right: if($global-text-direction == rtl, left, right);

// Internal map used to iterate through colors, to generate CSS classes with less code
$foundation-colors: (primary: $primary-color, secondary: $secondary-color, success: $success-color, alert: $alert-color, warning: $warning-color) !default;

@mixin foundation-global-styles {
  @include -zf-normalize;

  // These styles are applied to a <meta> tag, which is read by the Foundation JavaScript
  .foundation-mq {
    font-family: #{-zf-bp-serialize($breakpoints)}; }

  html {
    font-size: $global-font-size;
    box-sizing: border-box; }

  // Set box-sizing globally to handle padding and border widths
  *,
  *:before,
  *:after {
    box-sizing: inherit; }

  // Default body styles
  body {
    padding: 0;
    margin: 0;
    font-family: $body-font-family;
    font-weight: $global-weight-normal;
    line-height: $global-lineheight;
    color: $body-font-color;
    background: $body-background;

    @if $body-antialiased {
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale; } }

  img {
    // Grid defaults to get images and embeds to work properly
    max-width: 100%;
    height: auto;
    -ms-interpolation-mode: bicubic;

    // Get rid of gap under images by making them display: inline-block; by default
    display: inline-block;
    vertical-align: middle; }

  // Make sure textarea takes on height automatically
  textarea {
    height: auto;
    min-height: 50px;
    border-radius: $global-radius; }

  // Make select elements are 100% width by default
  select {
    width: 100%;
    border-radius: $global-radius; }

  // Styles Google Maps and MapQuest embeds properly
  // scss-lint:disable IdSelector
  #map_canvas,
  .map_canvas,
  .mqa-display {
    img,
    embed,
    object {
      max-width: none !important; } }

  // Reset <button> styles created by most browsers
  button {
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    padding: 0;
    border: 0;
    border-radius: $global-radius;
    line-height: 1; } }

/// Loads normalize.css.
/// @access private
@mixin -zf-normalize {
  //! normalize.css v3.0.3 | MIT License | github.com/necolas/normalize.css

  //*
  //* 1. Set default font family to sans-serif.
  //* 2. Prevent iOS and IE text size adjust after device orientation change,
  //*    without disabling user zoom.

  html {
    font-family: sans-serif;

    // 1
    -ms-text-size-adjust: 100%;

    // 2
    -webkit-text-size-adjust: 100%;

 }    // 2

  //*
  //* Remove default margin.

  body {
    margin: 0; }

  // HTML5 display definitions
  //* ==========================================================================

  //*
  //* Correct `block` display not defined for any HTML5 element in IE 8/9.
  //* Correct `block` display not defined for `details` or `summary` in IE 10/11
  //* and Firefox.
  //* Correct `block` display not defined for `main` in IE 11.

  article,
  aside,
  details,
  figcaption,
  figure,
  footer,
  header,
  hgroup,
  main,
  menu,
  nav,
  section,
  summary {
    display: block; }

  //*
  //* 1. Correct `inline-block` display not defined in IE 8/9.
  //* 2. Normalize vertical alignment of `progress` in Chrome, Firefox, and Opera.

  audio,
  canvas,
  progress,
  video {
    display: inline-block;

    // 1
    vertical-align: baseline;

 }    // 2

  //*
  //* Prevent modern browsers from displaying `audio` without controls.
  //* Remove excess height in iOS 5 devices.

  audio:not([controls]) {
    display: none;
    height: 0; }

  //*
  //* Address `[hidden]` styling not present in IE 8/9/10.
  //* Hide the `template` element in IE 8/9/10/11, Safari, and Firefox < 22.

  [hidden],
  template {
    display: none; }

  // Links
  //* ==========================================================================

  //*
  //* Remove the gray background color from active links in IE 10.

  a {
    background-color: transparent; }

  //*
  //* Improve readability of focused elements when they are also in an
  //* active/hover state.

  a:active,
  a:hover {
    outline: 0; }

  // Text-level semantics
  //* ==========================================================================

  //*
  //* Address styling not present in IE 8/9/10/11, Safari, and Chrome.

  abbr[title] {
    border-bottom: 1px dotted; }

  //*
  //* Address style set to `bolder` in Firefox 4+, Safari, and Chrome.

  b,
  strong {
    font-weight: bold; }

  //*
  //* Address styling not present in Safari and Chrome.

  dfn {
    font-style: italic; }

  //*
  //* Address variable `h1` font-size and margin within `section` and `article`
  //* contexts in Firefox 4+, Safari, and Chrome.

  h1 {
    font-size: 2em;
    margin: 0.67em 0; }

  //*
  //* Address styling not present in IE 8/9.

  mark {
    background: #ff0;
    color: #000; }

  //*
  //* Address inconsistent and variable font size in all browsers.

  small {
    font-size: 80%; }

  //*
  //* Prevent `sub` and `sup` affecting `line-height` in all browsers.

  sub,
  sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline; }

  sup {
    top: -0.5em; }

  sub {
    bottom: -0.25em; }

  // Embedded content
  //* ==========================================================================

  //*
  //* Remove border when inside `a` element in IE 8/9/10.

  img {
    border: 0; }

  //*
  //* Correct overflow not hidden in IE 9/10/11.

  svg:not(:root) {
    overflow: hidden; }

  // Grouping content
  //* ==========================================================================

  //*
  //* Address margin not present in IE 8/9 and Safari.

  figure {
    margin: 1em 40px; }

  //*
  //* Address differences between Firefox and other browsers.

  hr {
    box-sizing: content-box;
    height: 0; }

  //*
  //* Contain overflow in all browsers.

  pre {
    overflow: auto; }

  //*
  //* Address odd `em`-unit font size rendering in all browsers.

  code,
  kbd,
  pre,
  samp {
    font-family: monospace, monospace;
    font-size: 1em; }

  // Forms
  //* ==========================================================================

  //*
  //* Known limitation: by default, Chrome and Safari on OS X allow very limited
  //* styling of `select`, unless a `border` property is set.

  //*
  //* 1. Correct color not being inherited.
  //*    Known issue: affects color of disabled elements.
  //* 2. Correct font properties not being inherited.
  //* 3. Address margins set differently in Firefox 4+, Safari, and Chrome.

  button,
  input,
  optgroup,
  select,
  textarea {
    color: inherit;

    // 1
    font: inherit;

    // 2
    margin: 0;

 }    // 3

  //*
  //* Address `overflow` set to `hidden` in IE 8/9/10/11.

  button {
    overflow: visible; }

  //*
  //* Address inconsistent `text-transform` inheritance for `button` and `select`.
  //* All other form control elements do not inherit `text-transform` values.
  //* Correct `button` style inheritance in Firefox, IE 8/9/10/11, and Opera.
  //* Correct `select` style inheritance in Firefox.

  button,
  select {
    text-transform: none; }

  //*
  //* 1. Avoid the WebKit bug in Android 4.0//* where (2) destroys native `audio`
  //*    and `video` controls.
  //* 2. Correct inability to style clickable `input` types in iOS.
  //* 3. Improve usability and consistency of cursor style between image-type
  //*    `input` and others.

  button,
  html input[type="button"],
  input[type="reset"],
  input[type="submit"] {
    -webkit-appearance: button;

    // 2
    cursor: pointer;

 }    // 3

  //*
  //* Re-set default cursor for disabled elements.

  button[disabled],
  html input[disabled] {
    cursor: default; }

  //*
  //* Remove inner padding and border in Firefox 4+.

  button::-moz-focus-inner,
  input::-moz-focus-inner {
    border: 0;
    padding: 0; }

  //*
  //* Address Firefox 4+ setting `line-height` on `input` using `!important` in
  //* the UA stylesheet.

  input {
    line-height: normal; }

  //*
  //* It's recommended that you don't attempt to style these elements.
  //* Firefox's implementation doesn't respect box-sizing, padding, or width.
  //
  //* 1. Address box sizing set to `content-box` in IE 8/9/10.
  //* 2. Remove excess padding in IE 8/9/10.

  input[type="checkbox"],
  input[type="radio"] {
    box-sizing: border-box;

    // 1
    padding: 0;

 }    // 2

  //*
  //* Fix the cursor style for Chrome's increment/decrement buttons. For certain
  //* `font-size` values of the `input`, it causes the cursor style of the
  //* decrement button to change from `default` to `text`.

  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    height: auto; }

  //*
  //* 1. Address `appearance` set to `searchfield` in Safari and Chrome.
  //* 2. Address `box-sizing` set to `border-box` in Safari and Chrome.

  input[type="search"] {
    -webkit-appearance: textfield;

    // 1
    box-sizing: content-box;

 }    // 2

  //*
  //* Remove inner padding and search cancel button in Safari and Chrome on OS X.
  //* Safari (but not Chrome) clips the cancel button when the search input has
  //* padding (and `textfield` appearance).

  input[type="search"]::-webkit-search-cancel-button,
  input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none; }

  //*
  //* Define consistent border, margin, and padding.

  fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em; }

  //*
  //* 1. Correct `color` not being inherited in IE 8/9/10/11.
  //* 2. Remove padding so people aren't caught out if they zero out fieldsets.

  legend {
    border: 0;

    // 1
    padding: 0;

 }    // 2

  //*
  //* Remove default vertical scrollbar in IE 8/9/10/11.

  textarea {
    overflow: auto; }

  //*
  //* Don't inherit the `font-weight` (applied by a rule above).
  //* NOTE: the default cannot safely be changed in Chrome and Safari on OS X.

  optgroup {
    font-weight: bold; }

  // Tables
  //* ==========================================================================

  //*
  //* Remove most spacing between table cells.

  table {
    border-collapse: collapse;
    border-spacing: 0; }

  td,
  th {
    padding: 0; } }
